import baseUrl from '../../config.js';
export function post (url,data,callBack) {
    let token=uni.getStorageSync('token');
	// token
	token=true;
	// TOKEN
	if (token) {
		token='Bearer '+token;
	}else{
		// 
		return false;
	}
	uni.request({
		url: baseUrl+url, //
		data:data?data:{},
		method:'POST',
		header: {
			'custom-header': 'X-Token' //
		},
		success: (res) => {
			if(callBack){
				callBack(res.data);
			}
			uni.hideLoading();
		},
		fail() {
			uni.hideLoading();
		}
	});
}
export function get (url,data,callBack) {
    let token=uni.getStorageSync('token');
	// token
	token=true;
	// TOKEN
	if (token) {
		token='Bearer '+token;
	}else{
		// 
		return false;
	}
	uni.request({
		url: baseUrl+url, //
		data:data?data:{},
		method:'GET',
		header: {
			'Authorization': token //
		},
		success: (res) => {
			if(callBack){
				callBack(res.data);
			}
			uni.hideLoading();
		},
		fail() {
			uni.hideLoading();
		}
	});
}
export function lAxios (url,type,data,callBack) {
	let token=uni.getStorageSync('token');
	// TOKEN
	if (token) {
		token='Bearer '+token;
	}else{
		// 
		// return false;
	}
	let promise=new Promise((resolve,reject)=>{
		uni.request({
			url: baseUrl+url, //
			data:data?data:{},
			method:type,
			header: {
				'Authorization': token //
			},
			success: (res) => {
				if(callBack){
					callBack(res.data);
				}
				uni.hideLoading();
				resolve(res.data);
			},
			fail() {
				uni.hideLoading();
			}
		});
	});
	return promise;
}